<template>
  <div>
    <!-- <h1>TestComponent - 테스트 진행하는 페이지</h1> -->
    <div v-if="questionPage<11">
      <!-- <p>테스트 진행중</p> -->

      <div v-for="question in questionList" :key="question">
        <div v-if="questionPage===question.id+1">
          <h2>{{ questionPage }}/10</h2>
      <div class="qbox" @click="$emit('questionAdd',1); nextQuestion()">{{ question.a }}</div>
      <div class="qbox" @click="$emit('questionAdd',0); nextQuestion()">{{ question.b }}</div>
        </div>
      </div>
      <!-- <h3 @click="$emit('questionAdd',1); nextQuestion()">질문1</h3>
    <h3 @click="$emit('questionAdd',2); nextQuestion()">질문2</h3> -->
    </div>
    <!-- <button v-if="questionPage!=10" @click="nextQuestion">다음질문</button> -->
    <button class="main-button" v-if="questionPage==11" @click="$emit('changePage',2)">테스트 결과 보러가기</button>

  </div>
</template>

<script>
export default {
    name: 'TestComponent',
    // 여기부터 props가없어도진행되는 이유는..?
    props:{
    page:Number,
    question:Array,
    questionList:Object,
  },
  data(){
    return{
      questionPage:1,
    }
  },
  methods:{
    nextQuestion(){
      if(this.questionPage<11){
        this.questionPage+=1;
      }
    },
  },
}
</script>

<style>
.qbox{
  box-sizing: border-box;
  font-family: "consolas";
  width: 100%;
  max-width: 350px;
  margin: auto;
  margin-bottom: 20px;
  position: relative;
  border: 2px solid #eee;
  border-radius: 10px;
  cursor: pointer;
}
.qbox:hover{
  border: 2px solid black;

}
</style>