<template>
  <!-- 여기 혹은 밑에 div에다가 class줘서 페이지 통일 시키기 -->
<div class="testApp">

  <img src="./assets/free-icon-apple-logo-94225.png" style="width: 100px;">
  <h2>앱등이테스트</h2>
  <hr style="margin-left:50px; margin-right:50px; margin-bottom: 20px;">
  <!-- <h1>AppComponent - 테스트이름</h1> -->
  <!-- <button @click="page=0">Main</button>
  <button @click="page=1">Test</button>
  <button  @click="page=2">Result</button> -->
  <MainCompoent v-if="page==0" :page="page" @changePage="page=1"></MainCompoent>
  <TestComponent v-if="page==1" :page="page" :questionList="questionList"
  @changePage="page=2" @questionAdd="questionAdd"></TestComponent>
  <ResultComponent v-if="page==2" :page="page" :total="total"
  @changePage="page=0;question=[];total=0"></ResultComponent>
</div>
</template>

<script>
import MainCompoent from './components/MainComponent.vue'
import TestComponent from './components/TestComponent.vue'
import ResultComponent from './components/ResultComponent.vue'
import questionList from './assets/questionList'

export default {
  name: 'App',
  components: {
    MainCompoent,
    TestComponent,
    ResultComponent,
  },
  data(){
    return{
      page:0,
      question:[],
      questionList:questionList,
      total:0,
    }
  },
  methods: {
    questionAdd(a){
      this.question.push(a);
      // console.log(this.question);
      this.total+=a;
      // console.log(this.total);
    },
    kakaoShare() {
      window.Kakao.Share.sendCustom({
        templateId: 93225,
        // 카카오톡이 설치 되지 않았을때 마켓으로 이동
        installTalk: true
      });},
  },
}
</script>

