<template>
  <div>
    <div ref="target">
    <!-- <h1>ResultComponent - 테스트 결과 페이지</h1> -->
    <!-- <p>테스트 결과입니다.</p> -->
    <!-- <h2>테스트 결과</h2> -->

    <!-- 10점인경우 -->
    <div v-if="total==10">
      <h4>[잡스가 당신을 찬양합니다]</h4>
      <img src="../assets/10.png" style="width: 200px;">
      <p>"무결점의 앱등이 바로 당신입니다."</p>
    </div>
    <!-- 8-9점인경우 -->
    <div v-if="total==8 || total ==9">
      <h4>[팀쿡의 돈줄]</h4>
      <img src="../assets/89.png" style="width: 200px;">
      <p>"팀쿡의 눈엔 당신이 돈으로 보입니다."</p>
    </div>
    <!-- 6-7점인경우 -->
    <div v-if="total==6 || total==7">
      <h4>[팀쿡의 엄지척]</h4>
      <img src="../assets/67.png" style="width: 200px;">
      <p>"당신은 앞으로 성장할 앱등이 입니다."</p>
    </div>
    <!-- 4-5점인경우 -->
    <div v-if="total==4 || total==5">
      <h4>[아무생각 없는 팀쿡]</h4>
      <img src="../assets/45.png" style="width: 200px;">
      <p>"흔하디 흔한 애플 유저"</p>
    </div>
    <!-- 0-3점인경우 -->
    <div v-if="total<4">
      <h4>[재드래곤의 웃음]</h4>
      <img src="../assets/03.png" style="width: 200px;">
      <p>"삼성을 한번 써보는건 어떠실까요?"</p>
    </div>
  </div>
    <!-- <div ref="chartcapture">테스트</div> -->
    <!-- 이미지 캡처 -->
    <!-- <button class="main-button" @click="copyChartImg">결과저장</button> -->
    <!-- 테스트 재시작 -->
    <!-- <a href="../assets/result/03.jpg" download="result">다운로드</a> -->
    <!-- 여기 절대경로로해서 이미지 다운로드 테스트해보자 github주소 ㅇㅇ 그냥 풀 url주소로는 안되는듯여트넽스 -->
    <br>
    <div ref="result"></div>
    <br>
    <button @click="myFunction" class="main-button">테스트 저장 하기</button>
    <br>
    <button class="main-button" @click="$emit('changePage',0)">테스트 다시 하기</button>
    <br>
    <button class="kakao-share" @click="kakaoShare">카카오 공유하기</button>


  </div>
</template>



<script>
import html2canvas from 'html2canvas';

export default {
  data(){
    return{
      capture:true,
    }
  },
    name: 'ResultComponent',
    props:{
    page:Number,
    total:Number,
  },
  methods:{
    kakaoShare() {
      window.Kakao.Share.sendCustom({
        templateId: 93225,
        // 카카오톡이 설치 되지 않았을때 마켓으로 이동
        installTalk: true
      });},
      myFunction(){
    // console.log('테스트!',this.$refs.target);
    // html2canvas(document.querySelector("#capture")).then(canvas => {
    // document.body.appendChild(canvas)});
        html2canvas(this.$refs.target).then(canvas=>{
      if(this.capture){
        this.$refs.result.appendChild(canvas);
      this.capture=false;
      }
      // console.log(canvas);
      
      // 이걸넣어줘야 한번만 캡처하지 ㅇㅇ
      canvas.toBlob(blob=>{ //캔버스 이미지 blob 데이터로 변환 //2
      navigator.clipboard.write([  //3
        new ClipboardItem({
          'image/png': blob //<- 복사할 blob 데이터
        })       
      ]);
      alert("이미지를 클립보드에 복사했어요!");
  });
    })
      

  }
  }
}
</script>

